.app-container {
  font-family: 'Arial, sans-serif';
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
  background-color:#f0f4f8;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  }
  
  .heading {
  text-align: center;
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
  }
  
  .search-options-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  margin-bottom: 20px;
  }
  
  .input-group {
  display: flex;
  align-items: center;
  }
  
  .input-field {
  padding: 12px;
  flex: 1;
  border-radius: 8px;
  border: 1px solid #ccc;
  margin-right: 10px;
  font-size: 1em;
  }
  
  .search-button {
  padding: 12px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .search-button:hover {
  background-color: #45a049;
  transform: scale(1.05);
  }
  
  .song-options-container {
  margin-top: 20px;
  }
  
  .song-options-row {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  }
  
  .song-option {
  display: flex;
  align-items: center;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  width: calc(25% - 15px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .song-option:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  }
  
  .song-image {
  width: 60px;
  height: 60px;
  margin-right: 15px;
  border-radius: 8px;
  }
  
  .large-song-image {
  width: 80px;
  height: 80px;
  margin-right: 20px;
  border-radius: 8px;
  }
  
  .song-details {
  flex: 1;
  }
  
  .song-actions {
  display: flex;
  gap: 10px;
  }
  
  .add-icon, .preview-icon {
  color: #007bff;
  cursor: pointer;
  font-size: 1.2em;
  transition: color 0.3s ease;
  }
  
  .add-icon:hover, .preview-icon:hover {
  color: #0056b3;
  }
  
  .remove-icon {
  color: #e74c3c;
  cursor: pointer;
  transition: color 0.3s ease;
  }
  
  .remove-icon:hover {
  color: #c0392b;
  }
  
  .download-list-container {
  margin-top: 20px;
  }
  
  .download-list-row {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  }
  
  .download-list-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  width: calc(25% - 15px);
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .download-list-item:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  }
  
  .download-button {
  padding: 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 20px;
  font-size: 1.1em;
  transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .download-button:hover {
  background-color: #0056b3;
  transform: scale(1.05);
  }
  
  .full-screen-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  }
  
  .loading-container {
  text-align: center;
  background-color: #fff;
  padding: 25px;
  border-radius: 12px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);
  }
  
  .progress-bar {
  width: 100%;
  background-color: #ddd;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 15px;
  }
  
  .progress {
  height: 20px;
  background-color: #007bff;
  transition: width 0.3s;
  }
  
  @keyframes progressAnimation {
  0% { background-position: 0% 50%; }
  100% { background-position: 100% 50%; }
  }
  
  .progress {
  background: linear-gradient(270deg, #007bff, #0056b3);
  background-size: 400% 400%;
  animation: progressAnimation 2s infinite linear;
  }
  
  .download-complete-container {
  text-align: center;
  margin-top: 20px;
  font-size: 1.2em;
  color: #4CAF50;
  }